:root {
  font-family: Roboto, sans-serif;
}

li {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.modal-filmoteka {
  opacity: 1;
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-window {
