  top: 8px;
  right: 8px;
}

.modal-close-btn path {
  stroke: #000;
}

.modal-poster {
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px;
  width: 240px;
  height: 357px;
  margin-bottom: 20px;
  display: block;
}

.modal-poster-img {
  object-fit: cover;
